.menu-our-team {
  margin-top: 60px;
}
.menu-our-team h1 {
  padding: 80px 0;

  text-align: center;
  color: #7d0909;
  font-size: 32px;
  text-transform: inherit;
  letter-spacing: 1px;
}
.out-team-flip-card {
  display: flex;
  flex-wrap: wrap;
}
.flip-card {
  height: 330px;
  width: 23%;
  margin: 0 1%;
  margin-bottom: 2%;
  background-color: transparent;
}
.flip-card:hover {
  width: 23%;
}
.flip-card-inner {
  position: relative;
  height: 100%;
  text-align: center;
  transition: transform 0.8s;
  transform-style: preserve-3d;
}

.flip-card:hover .flip-card-inner {
  transform: rotateY(180deg);
  width: 100%;
}
.flip-card-front,
.flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden;
}
.flip-card-front {
  background-color: #ffffff;
  border: 1px solid #e0e0e0;
  text-align: center;
}
.flip-card-front img {
  object-fit: contain;
}
.flip-card-front h3,
.flip-card-back h3 {
  text-transform: uppercase;
  font-size: 15px;
  color: #444343;
}
.flip-card-front p {
  font-weight: bold;
  color: #7d0909;
}
.flip-card-back p {
  margin-bottom: 20px;
  color: #333;
  font-size: 16px;
}
.flip-card-back {
  transform: rotateY(180deg);
  background-color: #f7f7f7;
  border: 1px solid #000000;
}
@media only screen and (max-width: 1000px) {
  .flip-card,
  .flip-card:hover {
    width: 31%;
  }
}
@media only screen and (max-width: 766px) {
  .flip-card,
  .flip-card:hover {
    width: 46%;
    margin: 0 27%;
  }
  .out-team-flip-card {
    justify-content: center;
  }
  .menu-our-team,
  .we-are {
    margin-top: 100px;
  }
}
@media only screen and (max-width: 500px) {
  .flip-card,
  .flip-card:hover {
    width: 100%;
    margin: 0 2%;
  }
}

